<template>
	<view class="page">
		<view class="container">
			<!-- ËÆ¢Âçï‰ø°ÊÅØ -->
			<view class="order-section">
				<view class="section-title">
					<text class="title-icon">üìã</text>
					<text>ËÆ¢Âçï‰ø°ÊÅØ</text>
				</view>
				<view class="order-info">
					<view class="info-row">
						<text class="label">ËÆ¢ÂçïÁºñÂè∑</text>
						<view class="order-no-row">
							<text class="value">{{ orderNo }}</text>
							<button class="order-no-copy-btn" @click="handleCopyOrderNo" hover-class="button-hover">Â§çÂà∂</button>
						</view>
					</view>
					<view class="info-row">
						<text class="label">ÂïÜÂìÅÂêçÁß∞</text>
						<text class="value">{{ productName }}</text>
					</view>
					<view class="info-row">
						<text class="label">Ë¥≠‰π∞Êï∞Èáè</text>
						<text class="value">{{ quantity }}</text>
					</view>
				</view>
			</view>
			
			<!-- ÊîØ‰ªòÈáëÈ¢ù -->
			<view class="amount-section">
				<view class="amount-label">ÊîØ‰ªòÈáëÈ¢ù</view>
				<view class="amount-value">
					<text class="currency">¬•</text>
					<text class="amount">{{ payAmount }}</text>
				</view>
			</view>
			
			<!-- ÊîØ‰ªòÊñπÂºè -->
			<view class="payment-method-section">
				<view class="section-title">
					<text class="title-icon">üí≥</text>
					<text>ÊîØ‰ªòÊñπÂºè</text>
				</view>
				<view class="payment-list">
					<view class="payment-item selected">
						<view class="payment-left">
							<view class="wechat-icon">
								<text class="wechat-text">ÂæÆ‰ø°</text>
							</view>
							<text class="payment-name">ÂæÆ‰ø°ÊîØ‰ªò</text>
						</view>
						<view class="payment-check">‚úì</view>
					</view>
				</view>
				<view class="payment-tip">
					<text class="tip-text">* ÂΩìÂâç‰ªÖÊîØÊåÅÂæÆ‰ø°ÊîØ‰ªò</text>
				</view>
			</view>
		</view>
		
		<!-- Â∫ïÈÉ®ÊîØ‰ªòÊåâÈíÆ -->
		<view class="bottom-bar">
			<view class="total-info">
				<text class="total-label">ÂêàËÆ°Ôºö</text>
				<text class="total-amount">¬•{{ payAmount }}</text>
			</view>
			<button class="pay-button" @click="handlePay" :loading="paying" hover-class="button-hover">Á°ÆËÆ§ÊîØ‰ªò</button>
		</view>
	</view>
</template>

<script>
import { createWxPayOrder } from '../../api/order.js';

export default {
	data() {
		return {
			orderId: null,
			orderNo: '',
			productName: '',
			quantity: 1,
			payAmount: 0,
			addressId: null,
			paying: false
		}
	},
	onLoad(options) {
		if (options.orderId) {
			this.orderId = options.orderId;
		}
		if (options.orderNo) {
			this.orderNo = decodeURIComponent(options.orderNo);
		}
		if (options.productName) {
			this.productName = decodeURIComponent(options.productName);
		}
		if (options.quantity) {
			this.quantity = parseInt(options.quantity);
		}
		if (options.payAmount) {
			this.payAmount = parseFloat(options.payAmount);
		}
		if (options.addressId) {
			this.addressId = options.addressId;
		}
	},
	methods: {
		handleCopyOrderNo() {
			if (!this.orderNo) {
				return;
			}
			uni.setClipboardData({
				data: this.orderNo,
				success: () => {
					uni.showToast({
						title: 'ËÆ¢ÂçïÂè∑Â∑≤Â§çÂà∂',
						icon: 'success'
					});
				},
				fail: () => {
					uni.showToast({
						title: 'Â§çÂà∂Â§±Ë¥•',
						icon: 'none'
					});
				}
			});
		},
		
		// Â§ÑÁêÜÊîØ‰ªò
		async handlePay() {
			if (!this.orderId) {
				uni.showToast({
					title: 'ËÆ¢Âçï‰ø°ÊÅØÈîôËØØ',
					icon: 'none'
				});
				return;
			}
			
			if (this.paying) return; // Èò≤Ê≠¢ÈáçÂ§çÊèê‰∫§
			
			this.paying = true;
			
			try {
				uni.showLoading({
					title: 'ÊãâËµ∑ÊîØ‰ªò...',
					mask: true
				});
				
				// ÂêëÂêéÁ´ØËØ∑Ê±ÇÂæÆ‰ø°È¢ÑÊîØ‰ªòÂèÇÊï∞
				const payParams = {
					orderId: this.orderId
				};
				
				if (this.addressId) {
					payParams.addressId = this.addressId;
				}
				
				const wxPayData = await createWxPayOrder(payParams);
				console.log('Ëé∑ÂèñÂà∞ÁöÑÊîØ‰ªòÂèÇÊï∞:', wxPayData);
				
				// Ë∞ÉËµ∑ÂæÆ‰ø°ÊîØ‰ªò
				await new Promise((resolve, reject) => {
					const paymentOption = {
						timeStamp: String(wxPayData.timeStamp),
						nonceStr: wxPayData.nonceStr,
						package: wxPayData.packageVal,
						signType: wxPayData.signType || 'RSA',
						paySign: wxPayData.paySign,
						success: (res) => {
							console.log('requestPayment ÊàêÂäü:', res);
							resolve(res);
						},
						fail: (err) => {
							console.error('requestPayment Â§±Ë¥•:', err);
							reject(err);
						}
					};
					console.log('ÂèëËµ∑ÊîØ‰ªòÂèÇÊï∞:', paymentOption);
					uni.requestPayment(paymentOption);
				});
				
				uni.hideLoading();
				
				// ÊîØ‰ªòÊàêÂäüÊèêÁ§∫
				uni.showModal({
					title: 'ÊîØ‰ªòÊàêÂäü',
					content: 'ÊîØ‰ªòÊàêÂäüÔºåÂÖëÊç¢Á†ÅÁîüÊàêÂèØËÉΩÊúâÂá†ÁßíÂª∂ËøüÔºåÂèØÂú®ËÆ¢ÂçïËØ¶ÊÉÖÊü•Áúã',
					confirmText: 'Êü•ÁúãËØ¶ÊÉÖ',
					cancelText: 'Á®çÂêéÊü•Áúã',
					success: (res) => {
						if (res.confirm) {
							uni.redirectTo({
								url: `/pages/order-detail/order-detail?id=${this.orderId}`
							});
						} else {
							uni.redirectTo({
								url: '/pages/my-orders/my-orders'
							});
						}
					}
				});
				
			} catch (error) {
				uni.hideLoading();
				console.error('ÊîØ‰ªòÂ§±Ë¥•:', error);
				uni.showToast({
					title: (error && error.errMsg) || error.message || 'ÊîØ‰ªòÂ§±Ë¥•',
					icon: 'none',
					duration: 2000
				});
			} finally {
				this.paying = false;
			}
		}
	}
}
</script>

<style scoped lang="scss">
.page {
	min-height: 100vh;
	background-color: #f5f5f5;
	padding-bottom: 140rpx;
}

.container {
	padding: 20rpx 0;
}

.section-title {
	display: flex;
	align-items: center;
	font-size: 28rpx;
	font-weight: bold;
	color: #333333;
	padding: 20rpx 30rpx;
	background-color: #ffffff;
	border-bottom: 1rpx solid #f0f0f0;
}

.title-icon {
	font-size: 28rpx;
	margin-right: 8rpx;
}

/* ËÆ¢Âçï‰ø°ÊÅØ */
.order-section {
	background-color: #ffffff;
	margin-bottom: 20rpx;
	box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.05);
}

.order-info {
	padding: 20rpx 30rpx;
}

.info-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 20rpx;
	padding: 12rpx 0;
}

.info-row:last-child {
	margin-bottom: 0;
}

.label {
	font-size: 28rpx;
	color: #999999;
}

.info-row .value {
	font-size: 28rpx;
	color: #333333;
	font-weight: 500;
}

.order-no-row {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	gap: 16rpx;
}

.order-no-copy-btn {
	height: 56rpx;
	padding: 0 24rpx;
	background-color: #ffffff;
	color: #297512;
	font-size: 24rpx;
	border-radius: 28rpx;
	border: 2rpx solid #297512;
	line-height: 56rpx;
}

.order-no-copy-btn::after {
	border: none;
}

/* ÊîØ‰ªòÈáëÈ¢ù */
.amount-section {
	background-color: #ffffff;
	padding: 50rpx 30rpx;
	margin-bottom: 20rpx;
	text-align: center;
	box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.05);
}

.amount-label {
	font-size: 28rpx;
	color: #666666;
	margin-bottom: 20rpx;
}

.amount-value {
	display: flex;
	align-items: baseline;
	justify-content: center;
}

.currency {
	font-size: 40rpx;
	font-weight: bold;
	color: #90d26c;
	margin-right: 4rpx;
}

.amount {
	font-size: 80rpx;
	font-weight: bold;
	color: #90d26c;
	line-height: 1;
}

/* ÊîØ‰ªòÊñπÂºè */
.payment-method-section {
	background-color: #ffffff;
	margin-bottom: 20rpx;
	box-shadow: 0 2rpx 8rpx rgba(0, 0, 0, 0.05);
}

.payment-list {
	padding: 20rpx 30rpx;
}

.payment-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 24rpx 20rpx;
	background-color: #f5f5f5;
	border-radius: 12rpx;
	border: 2rpx solid #e0e0e0;
}

.payment-item.selected {
	background: linear-gradient(135deg, #f0f9f0 0%, #ffffff 100%);
	border-color: #90d26c;
}

.payment-left {
	display: flex;
	align-items: center;
	flex: 1;
}

.wechat-icon {
	width: 60rpx;
	height: 60rpx;
	background: linear-gradient(135deg, #09bb07 0%, #07c160 100%);
	border-radius: 12rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	margin-right: 16rpx;
}

.wechat-text {
	font-size: 22rpx;
	color: #ffffff;
	font-weight: bold;
}

.payment-name {
	font-size: 30rpx;
	color: #333333;
	font-weight: 500;
}

.payment-check {
	width: 40rpx;
	height: 40rpx;
	border-radius: 20rpx;
	background: linear-gradient(135deg, #a8e063 0%, #297512 100%);
	color: #ffffff;
	font-size: 24rpx;
	font-weight: bold;
	display: flex;
	align-items: center;
	justify-content: center;
}

.payment-tip {
	padding: 10rpx 30rpx 20rpx;
}

.tip-text {
	font-size: 24rpx;
	color: #999999;
}

/* Â∫ïÈÉ®ÊîØ‰ªòÊåâÈíÆ */
.bottom-bar {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	background-color: #ffffff;
	padding: 20rpx 30rpx;
	box-shadow: 0 -2rpx 10rpx rgba(0, 0, 0, 0.05);
	display: flex;
	align-items: center;
	justify-content: space-between;
	z-index: 100;
}

.total-info {
	flex: 1;
}

.total-label {
	font-size: 28rpx;
	color: #666666;
	margin-right: 10rpx;
}

.total-amount {
	font-size: 36rpx;
	font-weight: bold;
	color: #90d26c;
}

.pay-button {
	width: 280rpx;
	height: 80rpx;
	background: linear-gradient(135deg, #a8e063 0%, #297512 100%);
	border-radius: 40rpx;
	font-size: 32rpx;
	font-weight: bold;
	color: #ffffff;
	border: none;
	line-height: 80rpx;
	padding: 0;
}

.pay-button::after {
	border: none;
}

.button-hover {
	opacity: 0.85;
}
</style>
